<template>
    <div class="select-template" v-loading="createLoading">
        <div :style="h?'height:'+h+'px;':'height:480px;'" class="template-list">
            <div class="item" v-if="isShowDiy">
                <div class="template-image-content">
                    <img class="template-image" src="@/assets/img/diy.png"/>
                </div>
                <div class="template-info">
                    <div class="template-title"><span class="template-title-text">自定义模板</span></div>
                    <div class="template-action clearfix">
                        <el-button size="mini" plain @click="dialogVisible=true;tmpId=0">立即创建</el-button>
                    </div>
                </div>
            </div>
            <div class="item" v-for="(val,key) in datas.list" :key="key">
                <div class="template-image-content">
                    <img class="template-image" :src="val.photo"/>
                </div>
                <div class="template-info">
                    <div class="template-title"><span class="template-title-text">{{val.title}}</span></div>
                    <div class="template-action clearfix">
                        <!--class  xm  限免   mf  免费-->
                        <span class="xm">限免</span>
                        <el-button size="mini" @click="dialogVisible=true;tmpId=val.template_id" plain>立即应用</el-button>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog
                v-loading="createLoading"
                :modal-append-to-body="false"
                :modal="false"
                title="温馨提示"
                :visible.sync="dialogVisible"
                width="30%"
                size="mini" >
            <span>确认使用此模板吗？</span>
            <span slot="footer" class="dialog-footer">
                 <el-button      size="mini" @click="dialogVisible = false;tmpId=0;">取 消</el-button>
                <el-button     size="mini" type="primary" @click="creatgePage">确 定</el-button>
            </span>
        </el-dialog>
        <el-pagination :total="datas.count" :current-page="1" :page-size="datas.limit" @current-change="getTemplateList"></el-pagination>
    </div>
</template>

<script>
    import * as api from "@/api";

    export default {
        name: 'selectTemplate',
        props: ['h'],
        data() {
            return {
                dialogVisible: false,
                createLoading: false,
                tmpId:0,
                datas: [],
                isShowDiy: true,
            };
        },
        created() {
            this.getTemplateList();
        },
        methods: {
            getTemplateList(page = 1) {
                this.isShowDiy = page == 1;
                this.createLoading = true;
                api.post("/shop/admin.app.templates/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                }).then(res => {
                    this.datas.list = res.list;
                    this.datas.limit = res.limit;
                    this.datas.count = res.count;
                    this.createLoading = false;
                }).catch((error) => {
                    this.createLoading = false;
                });
            },

            creatgePage() {
                this.createLoading = true;
                api.post("/shop/admin.app.pages/create", {
                    appToken: this.$store.getters.appToken,
                    tmp_id: this.tmpId,
                }).then(res => {
                    this.$router.push("/design/" + res.page_id);
                    this.createLoading = false;
                }).catch((error) => {
                    this.createLoading = false;
                });
            },
        }
    }
</script>

<style scoped>
    .select-template {
        height: auto;
        overflow: hidden;
        width: 100%;
    }

    .template-list {
        overflow-y: scroll;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 20px;
        padding: 10px 0;
    }

    .template-list .item {
        width: 190px;
        height: 340px;
        margin-right: 20px;
        margin-bottom: 20px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        border: 1px solid #f2f2f2;
        -webkit-transition: .3s ease-in-out;
        -moz-transition: .3s ease-in-out;
        transition: .3s ease-in-out;
        border-radius: 4px;
        -webkit-box-shadow: 0 2px 12px #e5e5e5;
        box-shadow: 0 2px 12px #e5e5e5;
    }

    .template-list .item:hover {
        -webkit-transform: translateY(-10px);
        -moz-transform: translateY(-10px);
        -ms-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    .template-image-content {
        padding: 5px;
        position: relative;
        width: 178px;
        height: 238px;
        overflow: hidden;
        margin: 0 auto;
    }

    .template-image {
        display: block;
        background-size: cover;
        max-width: 100%;
        height: 238px;
        width: 100%;
        vertical-align: middle;
        position: relative;
    }

    .template-info {
        background: #fff;
    }

    .template-action {
        padding: 10px;
    }

    .template-info .template-title {
        padding: 10px;
        color: #000;
    }

    .template-action span {
        float: left;

        padding: 2px 5px;
        border-radius: 5px;
    }

    .template-action .el-button {
        float: right;
    }

    .xm {
        border: 1px solid #ff8417;
        color: #ff8417;
    }

    .mf {
        color: #0ccc6d;
        border: 1px solid #0ccc6d;
    }
</style>
