
<template>
  <div >
    <top-nav  title="微页"></top-nav>

    <div  class="wyu-main">
      <div class="wyu-block">
        <div class="wyu-tips wyu-tips-warning">
            <p class="tit">程序页面装修，没有装修页面首页将会是空白</p>
            <p>这里可以装修首页以及专题页面；其他功能页面会跟随首页的风格来自适应。预览网页版本可以看到装修的效果。</p>
        </div>

        <el-dialog title="选择模版" :visible.sync="selectTemplate" :modal-append-to-body="false" width="890px">
            <select-template></select-template>
        </el-dialog>

        <div class="style-home-info mt10">
          <div class="icon">
            <span class="iconfont icon-index"></span>
            <router-link target="_blank" v-if="datas.default.page_id != undefined" :to="'/design/'+ datas.default.page_id">
              <el-button type="text">编辑</el-button>
            </router-link>
          </div>

            <div class="info">
                <div class="tit">店铺首页</div>
                <p>小程序H5等首页的可视化操作;可以拖拽实现首页的布局。</p>
            </div>
        </div>
        <el-button v-if="datas.count < 20" size="small" type="primary"  @click="selectTemplate = true" style="margin-top:10px;margin-bottom:10px;">新增微页</el-button>
          <el-table :data="datas.list" style="width: 100%" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="page_id" label="微页Id" width="80"></el-table-column>
            <el-table-column prop="title" label="微页标题"></el-table-column>

            <el-table-column label="预览图">
              <template slot-scope="scope">
                <el-popover placement="top-start" :title="scope.row.title" trigger="hover">
                  <img :src=scope.row.photo width="200" height="400" />
                  <img :src=scope.row.photo slot="reference" width="25" height="50" />
                </el-popover>
              </template>
            </el-table-column>



            <el-table-column label="是否默认">
              <template slot-scope="scope">
                  <el-switch :disabled="scope.row.is_default == 1" :value="scope.row.is_default"  :active-value="1" :inactive-value="0" @change="setDefault(scope)" ></el-switch>
              </template>
            </el-table-column>

            <el-table-column label="时间" >
              <template slot-scope="scope">
                <p>创建时间:{{scope.row.add_time}}</p>
                <p>最后更新:{{scope.row.last_time}}</p>
              </template>
            </el-table-column>

          <el-table-column label="操作" >
            <template slot-scope="scope">
              <router-link :to="'/design/' + scope.row.page_id" style="color: #ffffff;"><el-button size="mini" type="text">编辑</el-button></router-link>
              <el-popover style="margin: 0 10px;" placement="top" width="160" v-model="scope.row.is_delete">
                <p style="margin-bottom: 4px">确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                  </el-button>
                  <el-button :loading="loading" type="primary" size="mini" @click="deletePage(scope)">确定
                  </el-button>
                </div>
                <el-button type="text" slot="reference">删除</el-button>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
        <br/>
        <div class="page">
          <!--<div class="page-tool">-->
            <!--<el-button icon="el-icon-delete" size="mini" type="danger" @click="deletePagesByIds">批量删除</el-button>-->
          <!--</div>-->
          <div class="page-mian">
            <el-pagination @current-change="getTemplateList" :page-size="datas.limit"
                    layout="total, prev, pager, next" :total="datas.count">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
 import TopNav from "@/components/TopNav";
 import SelectTemplate from "./components/SelectTemplate";
 import * as api from "@/api";
export default {
  name: 'index',
    components: {TopNav,SelectTemplate},
    data(){
      return {
        selectTemplate:false,
        datas:{
          loading: false,
          default: {},
          limit: 10,
          count: 0,
          page:0,
          list:[],
          // theme:{
          //    navigationBarBackgroundColor:'#00B5DE',//顶部导航的背景颜色
          //    navigationBarFrontColor     : '#ffffff',//顶部导航的字体颜色只能黑色和白色
          //    mainColor                  : '#00B5DE',//主要颜色
          //    secondaryColor             : '#1ADCF1',//第二颜色
          //    tipsColor                 : '#FFAE19',//提示性的颜色
          //    statusColor               : '#FF3200',
          // }
        },
      };
    },

    created () {
      this.getTemplateList(1);
    },

    methods:{

      getTemplateList(page = 1){
        this.loading = true;
        api.post("/shop/admin.app.pages/index", {
            appToken: this.$store.getters.appToken,
            page: page,
        }).then(res => {
            this.datas.list = res.list;
            this.datas.limit = res.limit;
            this.datas.count = res.count;
            this.datas.default = res.default;
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        });
      },

      isdefault(row){
          return row.is_default == 0;
      },

      setDefault(scope){
           this.loading = true;
           let list = this.datas.list;
           let row = scope.row;
            api.post("/shop/admin.app.pages/setDefault", {
                appToken: this.$store.getters.appToken,
                page_id: row.page_id,
            }).then(res => {
                this.loading = false;
                list.forEach((item,index) => {
                    if(index != scope.$index){
                        item.is_default = 0;
                    }
                });
                scope.row.is_default= 1;
                this.$message({
                    message: '恭喜你，操作成功',
                    type: 'success',
                });
            }).catch(() => {
                this.loading = false;
            });

      },



      deletePage(scope){
          this.loading = true;
          api.post("/shop/admin.app.pages/del", {
              appToken: this.$store.getters.appToken,
              page_id: scope.row.page_id,
          }).then(() => {
              this.loading = false;
              this.$message({
                  message: '恭喜你，操作成功',
                  type: 'success',
              });
              this.getTemplateList();
          }).catch(() => {
              this.loading = false;
          });
      },

      deletePagesByIds(){

      },

      handleSelectionChange(){

      },

      getList(){

      },
    },
}
</script>

<style scoped>
    .style-home-info{
        padding: 10px 20px;
        background: #f2f2f2;
        height: auto;
        overflow: hidden;
    }

    .style-home-info span{

        height: 40px;
        width: 40px;
        font-size: 20px;
        text-align: center;
        line-height: 40px;
        background: #fff;
        display: block;
        color:red;
    }

    .style-home-info .info{
        float: left;
        margin-left: 20px;
    }
    .style-home-info p{
        font-size: 13px;
        color:#909399;
    }
    .style-home-info .info .tit{
        padding: 5px 10px;
        background: #005DE5;
        color:#fff;
        font-size: 14px;
        display: inline-block;
        margin-bottom: 10px;
        border-radius: 5px;
    }
    .style-home-info .icon{
        float: left;
        text-align: center;
    }
</style>
